html {
  background-color: #fafbfd;
}

body {
  color: #464242;
  margin: 0;
  padding: 0;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 18px;
}

h1 {
  text-align: center;
  font-size: 2rem;
}

h2 {
  text-align: center;
  font-size: 1.8rem;
}

h3 {
  font-size: 1.4rem;
}

.saltar-aqui a {
  color: #0248b1;
  position: absolute;
  font-size: .4rem !important;
}

header {
  border-bottom: 2px solid #4a7c0e;
  width: 100%;
  max-width: 800px;
  margin: 0% auto;
  padding: 10px;
}

.header-nav {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0% auto;
  display: flex;
}

.img-logo {
  width: 150px;
}

header a {
  color: #0248b1;
  border-radius: 5px;
  margin-right: 20px;
  padding: 5px;
  font-size: 1.4rem;
  font-weight: 700;
  text-decoration: none;
}

header a:hover {
  color: #a30021;
}

footer {
  background-color: #464242;
  margin-top: 50px;
  padding-top: 40px;
  padding-bottom: 40px;
}

footer p {
  text-align: center;
  color: #fff;
  margin: 0;
  font-size: 12px;
}

footer a {
  color: #fff;
}

.ref-icon {
  margin-top: 10px;
}

.icono-pers-senderismo {
  width: 50px;
  height: 50px;
}

main {
  width: 100%;
  max-width: 800px;
  margin: 0% auto;
  padding: 10px;
}

p {
  text-align: left;
  word-spacing: .16em;
  margin-bottom: 2em;
  font-size: 1rem;
  line-height: 1.5;
}

.img-portada {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: 400px;
}

.exclamar {
  text-align: center;
  font-weight: 700;
}

.caja-direccion {
  justify-content: center;
  align-items: center;
  display: flex;
}

.enlace-categoria {
  color: #009dff;
  border-bottom: 2px dashed;
  font-weight: 700;
  text-decoration: none;
}

.caja-direccion a:hover {
  color: #f03;
}

.caja-eco-iconos {
  text-align: center;
}

.caja-eco-iconos img {
  margin-left: 40px;
}

blockquote {
  text-align: justify;
  background-color: #f3f3f3;
  border: 2px solid #464242;
  border-radius: 10px;
  padding: 20px;
  display: flex;
}

.img-recorte {
  clip-path: polygon(0 50%, 10% 60%, 0 70%, 30% 100%, 70% 100%, 100% 70%, 90% 60%, 100% 50%, 90% 40%, 100% 20%, 70% 0, 30% 0, 0 20%, 10% 40%);
  width: 120px;
  height: auto;
}

.info-frase {
  padding: 20px;
}

.footer-img {
  color: #464242;
  background: #f3f3f3;
  margin: 0;
  padding: 0;
  font-size: .7rem;
}

.footer-img a {
  color: #009dff;
  border-bottom: 2px dashed;
  font-weight: 700;
  text-decoration: none;
}

.footer-img a:hover {
  color: red;
  border-bottom: 2px dashed;
  font-weight: 700;
  text-decoration: none;
}

.footer-cite {
  background-color: #f3f3f3;
  border-radius: 10px;
  place-content: center flex-end;
  margin: 0;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 40px;
  display: flex;
}

.footer-cite p {
  color: #464242;
  font-size: 1rem;
}

.corazon-animado {
  width: 30px;
  height: 30px;
}

.corazon-animado path {
  fill: #464242;
}

.corazon-palpitacion {
  transform-box: fill-box;
  transform-origin: center;
  animation: 2s ease-in-out infinite palpitacion;
}

@keyframes palpitacion {
  0%, 100% {
    transform: scale(1.5);
  }

  50% {
    transform: scale(1);
  }
}

.efecto-portada-svg {
  width: 100%;
  height: auto;
  font-size: 3rem;
  font-weight: 900;
}

.grupo-caracter {
  transform: translateX(20%);
}

.caracter {
  fill: #464242;
  animation: 1.5s ease-in-out infinite salto2;
}

.caracter:first-child {
  animation-delay: 0s;
}

.caracter:nth-child(2) {
  animation-delay: .12s;
}

.caracter:nth-child(3) {
  animation-delay: .24s;
}

.caracter:nth-child(4) {
  animation-delay: .48s;
}

.caracter:nth-child(5) {
  animation-delay: .24s;
}

.caracter:nth-child(6) {
  animation-delay: .12s;
}

.caracter:nth-child(7) {
  animation-delay: 0s;
}

@keyframes salto2 {
  0%, 100% {
    transform: translateY(10px);
  }

  30% {
    transform: translateY(-5px);
  }

  50% {
    transform: translateY(10px);
  }
}

.lista {
  text-align: center;
}

.direccion-del-arte img {
  transform-origin: center;
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  transition: transform .6s;
}

.lista img:hover {
  transform: scale(2);
}

.info-distancia {
  text-align: center;
  margin: 0 !important;
}

.caja-ver-mas {
  text-align: right;
  margin-top: 0;
}

.enlace-ver-mas {
  color: #0248b1;
  padding-right: 50px;
}

.enlace-ver-mas:hover {
  color: #a30021;
}

hr {
  border: none;
  border-top: 1px solid #ebebeb;
}

.img-representativa {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: 250px;
}

.fotos-galeria {
  justify-content: center;
  max-width: 800px;
  display: flex;
}

.fotos-galeria img {
  border-radius: 10px;
  width: 225px;
  height: 225px;
  margin: 25px;
}

.fotos-galeria img:hover {
  transform: scale(2);
}

iframe {
  border: none;
  border-radius: 10px;
  width: 100%;
  height: 500px;
}

#map {
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  height: 400px;
}

.pag-contactos {
  place-items: center;
  height: 100vh;
}

.contactos {
  text-align: center;
  background-color: #f3f3f3;
  border: 1px solid #464242;
  border-radius: 10px;
  padding: 20px;
}

.contactos p {
  text-align: center;
  margin: 0;
}

@media (width <= 900px) {
  blockquote {
    text-align: center;
    display: grid;
  }

  main {
    max-width: 900px;
  }

  header {
    border-bottom: 2px solid #4a7c0e;
    width: 100%;
    max-width: 800px;
  }

  .header-nav {
    flex-direction: column;
  }

  .header-nav nav {
    text-align: center;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    display: flex;
  }

  .header-nav nav a {
    color: #0248b1;
    font-size: 1.5rem;
    text-decoration: underline;
  }

  .lista img:hover {
    transform: none;
  }
}

@media (width <= 600px) {
  .caracter {
    animation: none;
  }

  main, footer, header {
    width: 100%;
  }

  h1 {
    font-size: 1.8rem;
  }

  h2 {
    font-size: 1.4rem;
  }

  h3 {
    font-size: 1rem;
  }

  main {
    max-width: 400px;
  }

  header {
    border-bottom: 2px solid #4a7c0e;
    width: 100%;
    max-width: 400px;
  }

  .header-nav {
    flex-direction: column;
  }

  .header-nav nav {
    text-align: center;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    display: flex;
  }

  .header-nav nav a {
    color: #0248b1;
    font-size: 1.5rem;
    text-decoration: underline;
  }

  .img-logo {
    max-width: 400px;
  }

  .lista img:hover {
    transform: none;
  }

  .fotos-galeria {
    flex-direction: column;
    align-items: center;
    max-width: 400px;
    display: flex;
  }

  .fotos-galeria img:hover {
    max-width: 300px;
    transform: scale(1.8);
  }
}
